@import url(https://cdn.jsdelivr.net/npm/bootstrap-icons@1.7.2/font/bootstrap-icons.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
html {
  background-color: white;
  height: 100vh;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

@media (max-width: 350px) {
  .element-to-hide-under-350 {
    display: none;
  }
}

@media (min-width: 350px) {
  .element-to-hide-over-350 {
    display: none;
  }
}

@media (max-width: 500px) {
  #cont {
    width: 95%;
    margin-left: auto;
    margin-right: auto;
  }
}

@media (min-width: 500px) {
  #cont {
    width: 90%;
    margin-left: auto;
    margin-right: auto;
  }
}

@media (min-width: 768px) {
  #body {
    width: 95%;
    margin-left: auto;
    margin-right: auto;
  }
  #cont {
    width: 80%;
    margin-left: auto;
    margin-right: auto;
  }
  .element-to-hide-over-768 {
    display: none;
  }
}

@media (min-width: 992px) {
  #body {
    width: 90%;
    margin-left: auto;
    margin-right: auto;
  }
  #cont {
    width: 70%;
    margin-left: auto;
    margin-right: auto;
  }
}

@media (min-width: 1100px) {
  #body {
    width: 85%;
    margin-left: auto;
    margin-right: auto;
  }
  #cont {
    width: 60%;
    margin-left: auto;
    margin-right: auto;
  }
}

@media (min-width: 1200px) {
  #body {
    width: 80%;
    margin-left: auto;
    margin-right: auto;
  }
}

@media (min-width: 1300px) {
  #body {
    width: 70%;
    margin-left: auto;
    margin-right: auto;
  }
  #cont {
    width: 50%;
    margin-left: auto;
    margin-right: auto;
  }
}

@media (min-width: 1400px) {
  #body {
    width: 60%;
    margin-left: auto;
    margin-right: auto;
  }
  #cont {
    width: 40%;
    margin-left: auto;
    margin-right: auto;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

input.remove-spin-box::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input.remove-spin-box::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number].remove-spin-box {
  -moz-appearance: textfield;
}
